$box-side: 100px;
$days-in-week: 7;
$side-padings: 10px;
$gap: 1px;
$full-box-size: $box-side + $gap;

$days-of-the-week:
  "mon" 0,
  "tue" ($full-box-size),
  "wed" ($full-box-size * 2),
  "thu" ($full-box-size * 3),
  "fri" ($full-box-size * 4),
  "sat" ($full-box-size * 5),
  "sun" ($full-box-size * 6);
